@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes increase {
  from {
    margin: 0 80px;
  }

  to {
    margin: 0;
  }
}

@keyframes slide-in {
  from {
    transform: translateY(75px);
  }

  to {
    transform: translateY(0);
  }
}
